const getColorByGenre = (genre: string): string => {
  let color: string;

  switch (genre) {
    case '':
      color = '#db8070';
      break;
    case '':
      color = '#495590';
      break;
    case '':
      color = '#a0a284';
      break;
    case '':
      color = '#be0b19';
      break;
    case '':
      color = '#e1914c';
      break;
    case '':
      color = '#5b6884';
      break;
    case '':
      color = '#e1b562';
      break;
    case '':
      color = '#96a7ba';
      break;
    case '':
      color = '#455a64';
      break;
    case '':
      color = '#5b3cf5';
      break;
    default:
      color = '#db8070';
      break;
  }

  return color;
};

export { getColorByGenre };
